<template>
    <div>
        <vx-card class="mb-base">
            <div class="md:flex my-4">
                <div class="lg:w-1/8 mt-4 md:mt-2 md:ml-6">
                    <label>Kategori</label>
                </div>
                <div class="lg:w-1/8 mt-4 md:mt-2 md:ml-6">
                    <vs-checkbox v-model="kategori" vs-value="luis">STAR</vs-checkbox>
                </div>
                <div class="lg:w-1/8 mt-4 md:mt-2 md:ml-6">
                    <vs-checkbox v-model="kategori" vs-value="carols">PARETO</vs-checkbox>
                </div>
                <div class="lg:w-1/6 mt-4 md:mt-0 md:ml-6">
                    <datepicker data-vv-validate-on="blur" :placeholder="'-- Tanggal Awal --'" :minimumView="'day'" :maximumView="'month'" :format="'dd MMM yyyy'" v-model="tanggalAwal" ></datepicker>
                </div>
                <div class="lg:w-1/6 mt-4 md:mt-0 md:ml-6">
                    <datepicker data-vv-validate-on="blur" :placeholder="'-- Tanggal Akhir --'" :minimumView="'day'" :maximumView="'month'" :format="'dd MMM yyyy'" v-model="tanggalAkhir" ></datepicker>
                </div>
                <div class="lg:w-1/6 mt-4 md:mt-0 md:ml-6">
                    <v-select data-vv-validate-on="blur" :placeholder="'-- Berdasarkan --'" :options="filter" v-model="berdasarkan"></v-select>
                </div>
                <div class="mt-4 md:mt-0 md:ml-6">
                    <vs-button :disabled="!validateForm" radius color="primary" icon="search" v-on:click="loadDataServer"  style="display:inline; margin-right:30px;"></vs-button>
                </div>   
            </div>
        </vx-card>

        <vx-card v-show="body.length > 0">
            <table class="zui-table" width="100%">
                <thead>
                    <tr class="bg-primary text-white">
                        <th  v-for="(items,index) in header" :key="index" class=" bg-primary text-white">{{items}}</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(value,key) in body" :key="key" >
                        <td class="text-center" v-for="(item,index) in value" :key="index" v-html="item"></td>
                    </tr>
                </tbody>
            </table>
        </vx-card>
    </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker'

export default {
    data(){
        return {
            kategori:[],
            tanggalAwal:'',
            tanggalAkhir:'',
            filter:['SALES','TRANSAKSI','COST & COGS','BUDGET'],
            berdasarkan:'',
            header:['data1','data2','data3'],
            body:[
                {
                    data1:"test1",
                    data2:"test2",
                    data3:"test3"
                },
                {
                    data1:"test1",
                    data2:"test2",
                    data3:"test3"
                }
            ]
        }
    },

    components:{Datepicker},

    computed: {
        validateForm() {
        return !this.errors.any() && this.tanggalAwal != '' && this.tanggalAkhir != '' && this.berdasarkan != '';
        },
    },

    methods:{
        loadDataServer(){
            if(this.kategori == []){
                this.$swal.fire(
                    'Oopss !',
                    'Anda Belum Memilih Kategori',
                    'error'
                )
            }else{
                this.$vs.loading();

                let postData = {
                    kategori : this.kategori,
                    tgl_awal:  btoa(this.tanggalAwal),
                    tgl_akhir: btoa(this.tanggalAkhir),
                }

                this.$http.post('/',postData)
                .then(response => {
                    this.head = response.data.data.head;
                    this.body = response.data.data.body
                })
                .catch(error => {error})
                .finally(() =>{this.$vs.loading.close()})
            }
        }
    }

}
</script>

<style scoped>
    .zui-table {
        border: none;
        border-collapse: separate;
        border-spacing: 0;
        font: normal 13px Arial, sans-serif;
    }
    .zui-table thead th {
        border: none;
        padding: 10px;
        text-align: center;
        white-space: nowrap;
    }
    .zui-table tbody td {
        border-bottom: solid 1px #DDEFEF;
        border-right: solid 1px #DDEFEF;
        border-left: solid 1px #DDEFEF;
        color: #333;
        padding: 10px;
        text-shadow: 1px 1px 1px #fff;
        white-space: nowrap;
    }
</style>